li,
ul {
  padding: 0;
  margin: 0; }

.popup-blur {
  -webkit-filter: blur(10px) grayscale(50%);
  filter: blur(5px) grayscale(50%); }

.menu {
  background-color: #ffffff;
  min-width: 320px;
  position: absolute;
  top: 0;
  height: auto;
  z-index: 100; }
  @media screen and (min-width: 1366px) {
    .menu {
      position: sticky; } }
  .menu .container-menu {
    padding: 0 16px; }
    @media screen and (min-width: 767px) {
      .menu .container-menu {
        padding: 0 24px; } }
    .menu .container-menu .menu__header {
      display: flex;
      padding-top: 16px;
      margin-bottom: 24px;
      align-items: center; }
      .menu .container-menu .menu__header .menu__burger-close {
        cursor: pointer;
        margin-right: 16px; }
        @media screen and (min-width: 1366px) {
          .menu .container-menu .menu__header .menu__burger-close {
            display: none; } }
      .menu .container-menu .menu__header .menu__logo {
        margin-right: auto; }
    .menu .container-menu .menu-nav {
      margin-bottom: 32px; }
      @media screen and (min-width: 767px) {
        .menu .container-menu .menu-nav {
          margin-bottom: 268px; } }
      .menu .container-menu .menu-nav .menu__items {
        padding: 0;
        margin: 0;
        display: flex;
        flex-direction: column;
        gap: 24px; }
        .menu .container-menu .menu-nav .menu__items .menu__item {
          position: relative;
          text-decoration: none; }
        .menu .container-menu .menu-nav .menu__items .menu__link {
          text-decoration: none;
          color: #1b1c21; }
          .menu .container-menu .menu-nav .menu__items .menu__link:hover {
            color: #7e7e82; }
          .menu .container-menu .menu-nav .menu__items .menu__link:hover:before {
            color: #7e7e82;
            content: ' ';
            left: -16px;
            top: -4px;
            position: absolute;
            width: 4px;
            height: 32px;
            border-radius: 0 5px 5px 0;
            background-color: #41f6d7; }
            @media screen and (min-width: 767px) {
              .menu .container-menu .menu-nav .menu__items .menu__link:hover:before {
                left: -24px; } }
    .menu .container-menu .menu__footer {
      padding-bottom: 32px; }
      .menu .container-menu .menu__footer .menu__info {
        display: flex;
        flex-direction: column; }
        .menu .container-menu .menu__footer .menu__info .menu__icons {
          display: flex;
          gap: 16px;
          margin-bottom: 16px; }
      .menu .container-menu .menu__footer .menu__link-email {
        margin-bottom: 12px;
        color: #1b1c21; }
      .menu .container-menu .menu__footer .menu__link-tel {
        margin-bottom: 8px;
        color: #1b1c21;
        font-weight: 700;
        font-size: 24px; }
        @media screen and (min-width: 767px) {
          .menu .container-menu .menu__footer .menu__link-tel {
            margin-bottom: 40px; } }
      .menu .container-menu .menu__footer .menu__lenguage {
        display: flex;
        gap: 7px; }
        .menu .container-menu .menu__footer .menu__lenguage .menu__link-leng {
          color: #1b1c21; }
          .menu .container-menu .menu__footer .menu__lenguage .menu__link-leng:hover {
            color: #7e7e82; }

.menu-deactive {
  left: 100%; }
  @media screen and (max-width: 1366px) {
    .menu-deactive {
      display: none; } }

.menu-active {
  left: 0; }

.popup-menu-click {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 10; }
