.footer-description {
  display: flex;
  padding: 16px;
  margin-top: 60px;
  justify-content: center; }

.footer-description__items {
  text-align: center; }
  @media screen and (min-width: 767px) {
    .footer-description__items {
      display: flex;
      flex-direction: row;
      justify-content: space-between; } }

.footer-description__item {
  max-width: 280px;
  margin-bottom: 16px; }
  @media screen and (min-width: 767px) {
    .footer-description__item {
      min-width: 230px;
      max-width: 230px;
      margin-right: 30px; } }
