.service {
  margin-bottom: 24px; }
  @media screen and (min-width: 767px) {
    .service {
      margin-bottom: 32px; } }
  @media screen and (min-width: 1366px) {
    .service {
      margin-top: 42px; } }
  @media screen and (min-width: 1366px) {
    .service .services__heading-container {
      display: flex;
      justify-content: space-between;
      margin-right: 30px; } }
  .service .services__heading-container .services__heading-wrapper .services__heading {
    position: relative;
    font-size: 28px;
    font-weight: 700;
    margin-left: 16px;
    margin-bottom: 24px; }
    @media screen and (min-width: 767px) {
      .service .services__heading-container .services__heading-wrapper .services__heading {
        margin-left: 24px; } }
    .service .services__heading-container .services__heading-wrapper .services__heading::before {
      content: ' ';
      position: absolute;
      z-index: 10;
      left: -24px;
      background-color: #41f6d7;
      width: 4px;
      height: 40px;
      border-radius: 0 5px 5px 0; }
  .service .desktop-popup {
    display: flex;
    gap: 32px; }
    @media screen and (max-width: 1366px) {
      .service .desktop-popup {
        display: none; } }
    .service .desktop-popup .desktop-popup__items {
      display: flex;
      flex-direction: row;
      align-items: center;
      gap: 8px; }
    .service .desktop-popup .desktop-popup__name-link {
      color: #1B1C21; }
  .service .services__list {
    display: flex;
    overflow: hidden;
    overflow-x: scroll;
    margin: 0 0 0 16px; }
    @media screen and (min-width: 767px) {
      .service .services__list {
        margin: 0 0 0 24px; } }
    .service .services__list::-webkit-scrollbar {
      overflow-x: scroll; }
      @media screen and (min-width: 767px) {
        .service .services__list::-webkit-scrollbar {
          overflow-wrap: normal; } }
    .service .services__list .services__item {
      padding: 8px 10px;
      border: 2px solid #f8f8f8;
      border-radius: 6px; }
      .service .services__list .services__item:hover {
        border: 2px solid #b8ffec;
        border-radius: 6px;
        background-color: #ffffff; }
      .service .services__list .services__item .services__link {
        white-space: nowrap;
        text-decoration: none;
        color: #7e7e82;
        font-weight: 400; }
  @media screen and (min-width: 767px) {
    .service .description {
      display: flex; } }
  .service .description .description__wrapper--left {
    padding: 0 16px; }
    @media screen and (min-width: 767px) {
      .service .description .description__wrapper--left {
        padding: 0 24px; } }
    .service .description .description__wrapper--left .description__container {
      overflow: hidden; }
      @media screen and (min-width: 767px) {
        .service .description .description__wrapper--left .description__container {
          margin-right: 26px; } }
      @media screen and (min-width: 1366px) {
        .service .description .description__wrapper--left .description__container {
          width: 490px; } }
    .service .description .description__wrapper--left .description__container--deactive {
      max-height: 160px; }
    .service .description .description__wrapper--left .description__services {
      font-size: 14px;
      font-weight: 400;
      margin-top: 10px;
      display: flex; }
    .service .description .description__wrapper--left .description__button {
      margin-top: 16px;
      font-weight: 500;
      background-color: #f8f8f8;
      cursor: pointer;
      margin-bottom: 16px;
      font-size: 14px;
      height: 24px;
      display: flex; }
    .service .description .description__wrapper--left .description__button-icon {
      display: flex;
      align-items: center;
      margin-right: 8px; }
    .service .description .description__wrapper--left .description__button-text {
      font-size: 16px; }
    .service .description .description__wrapper--left .description__button-icon--active {
      transform: rotate(180deg); }
  .service .description .description__image {
    display: inline-block;
    width: 100%;
    max-height: 307px; }
    @media screen and (min-width: 767px) {
      .service .description .description__image {
        border-radius: 5px;
        margin-right: 24px;
        margin-left: auto;
        max-width: 540px; } }
    @media screen and (min-width: 1366px) {
      .service .description .description__image {
        margin-left: 0; } }
