.header {
  min-width: 320px;
  height: 88px;
  background-color: #ffffff;
  border-bottom: 1px solid #b8ffec;
  margin-bottom: 24px; }
  @media screen and (min-width: 1366px) {
    .header {
      display: none; } }
  .header .container {
    padding: 0 16px; }
    @media screen and (min-width: 767px) {
      .header .container {
        padding: 0 24px; } }
  .header .header__wrapper {
    display: flex;
    padding: 19px 0;
    justify-content: space-between;
    align-items: center; }
  .header .header__wrapper-left {
    display: flex;
    align-items: center; }
    .header .header__wrapper-left .header__burger-open {
      cursor: pointer;
      width: 40px;
      height: 40px;
      background-color: #ffffff;
      margin-right: 16px; }
      @media screen and (min-width: 767px) {
        .header .header__wrapper-left .header__burger-open {
          margin-right: 0; } }
    .header .header__wrapper-left .header__logo {
      cursor: pointer; }
    .header .header__wrapper-left .header__decor-line--mobile-close {
      width: 2px;
      height: 32px;
      background-color: #eaeaea;
      border-radius: 1px;
      margin: 0 16px; }
      @media screen and (max-width: 767px) {
        .header .header__wrapper-left .header__decor-line--mobile-close {
          display: none; } }
  .header .header__wrapper-right {
    align-items: center;
    display: flex;
    justify-content: end; }
    .header .header__wrapper-right .header__button-call {
      cursor: pointer;
      margin-right: 16px; }
      @media screen and (max-width: 500px) {
        .header .header__wrapper-right .header__button-call {
          display: none; } }
    .header .header__wrapper-right .header__button-chat {
      cursor: pointer;
      margin-right: 16px; }
      @media screen and (max-width: 450px) {
        .header .header__wrapper-right .header__button-chat {
          display: none; } }
    .header .header__wrapper-right .header__button-profile {
      cursor: pointer; }
      @media screen and (max-width: 390px) {
        .header .header__wrapper-right .header__button-profile {
          display: none; } }
    .header .header__wrapper-right .header__decor-line {
      width: 2px;
      height: 32px;
      background-color: #eaeaea;
      border-radius: 1px;
      margin: 0 16px; }
    .header .header__wrapper-right .header__button-repair {
      cursor: pointer;
      margin-right: 16px;
      width: 40px;
      height: 40px; }
    .header .header__wrapper-right .header__button-checkstatus {
      cursor: pointer;
      width: 40px;
      height: 40px; }
