.swiper {
  width: 100%; }

@media screen and (min-width: 767px) {
  .swiper-wrapper {
    flex-wrap: wrap;
    gap: 16; } }

.swiper__button {
  margin-top: 24px;
  margin-left: 16px;
  background-color: #fbfbfb;
  cursor: pointer; }

.swiper-pagination {
  position: static;
  margin-top: 16px; }
  @media screen and (min-width: 1366px) {
    .swiper-pagination {
      display: none; } }

.swiper-pagination-bullet {
  background-color: #979797;
  width: 12px;
  height: 12px; }
